<template>
  <el-container>
    <el-header class="custom-header">
      <div class="title"></div>
      <!--      ,,
                srcElement-->
      <div class="homeRight">
        <el-badge :is-dot="is_dot">
          <el-button @click="chat" type="text" size="normal" icon="el-icon-bell"></el-button>
        </el-badge>
        <el-dropdown @command="commandHandler">
        <span class="el-dropdown-link">
           <span> {{ user.name }}</span>
            <i v-if="!user.userface" class="el-icon-arrow-down el-icon--right"></i>
            <i v-else><img :src="user.userface"/></i>
        </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="personal"></el-dropdown-item>
            <!--          -->
            <!--          <el-dropdown- command=item disabled></el-dropdown-item>-->
            <!--          item -->
            <el-dropdown-item command="signOut" divided></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <!--        Aside -->
        <!--   unique-opened     -->
        <el-menu class="custom-el-menu" router :unique-opened="true" :default-active="open">
          <!--          indexsubmenu,,-->
          <el-submenu :index="''+index" v-for="(item,index) in routes" v-if="!item.hidden">
            <template slot="title">
              <i style="color: #409eff;margin-right: 5px" :class="item.iconCls"></i>
              <span>{{ item.name }}</span>
            </template>
            <el-menu-item v-for="child in item.children"
                          :key="child.path"
                          :index="child.path">
              {{ child.name }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main>
        <!--        Main-->
        <el-breadcrumb separator="/" v-if="this.$route.name!='Home'">
          <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
          <el-breadcrumb-item>{{ this.$route.name }}</el-breadcrumb-item>
        </el-breadcrumb>
        <template v-else>
          <div class="home-welcome">
            
          </div>
        </template>
        <div class="router-view">
          <router-view/>
        </div>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>

import {createNamespacedHelpers} from 'vuex'

const {mapGetters: chatGetters} = createNamespacedHelpers('chat')


export default {
  name: "Home",
  async beforeMount() {
    await this.$store.dispatch('chat/initData');
    console.log('chat/initData-OK')
  },
  async created() {
    await this.$confirm(",", "")
    let user=this.$store.getters.user;
    this.utils.setImgCache(user.userface,user,"userface");
    this.$store.commit('user',user);
  },
  data: function () {
    return {
      open: window.location.hash.substr(1),
    }
  },
  computed: {
    routes: function () {
      return this.$store.getters.routes
    },
    user() {
      return this.$store.getters.user
    },
    ...chatGetters(['is_dot'])
  },
  methods: {
    commandHandler: function (command) {
      this[command]();
    },
    chat() {
      this.$router.replace('/chat')
    },
    personal() {
      this.$router.replace('/personal')
    },
    signOut() {
      let node = this;
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        node.$axios.execute('get', '/logout').then(data => {
              node.$store.dispatch('reset')
            }
        )
      }).catch(() => {
        node.$message({
          type: 'info',
          message: ''
        });
      });
    },

  }
}
</script>

<style>
.router-view {
  margin-top: 10px;
}

.home-welcome {
  text-align: center;
  font-size: 30px;
  font-family: ;
  color: #409eff;
  padding-top: 50px;
}

.custom-header {
  background: #409eff;
  /*flex: flex-grow flex-shrink flex-basis|auto|initial|inherit;*/
  display: flex;
  /*align-items flexflex*/
  align-items: center;
  /*justify-content: flex-start|flex-end|center|space-between|space-around|initial|inherit;*/
  /*justify-content */
  /*https://www.runoob.com/cssref/css3-pr-justify-content.html*/
  justify-content: space-between;
  padding: 0px 15px;
  /*	/ padding  border 
  */
  box-sizing: border-box;
}

.custom-header .title {
  font-size: 30px;
  font-family: ;
  color: #fff;
}

.homeRight .el-badge sup {
  height: 6px;
  width: 6px;
  border-radius: 10px;
  transform: translateX(3px);
}

.homeRight .el-button {
  color: black;
  /*font-size: inherit;*/
  margin-right: 8px;
  padding-top: 1px;
  padding-bottom: 1px;
}


.el-dropdown-link {
  cursor: pointer;
  color: #fff;
  display: flex;
  align-items: center;
}

.el-dropdown-link i img {
  width: 48px;
  height: 48px;
  border-radius: 24px;
  margin-left: 8px;
}

.el-icon-arrow-down {
  font-size: 12px;
}

.custom-el-menu {
  cursor: pointer
}
</style>